                    {{ alumnus.degree }}, {{ alumnus.year }}, {{ alumnus.supervision }}<br />
                    <template v-if="alumnus.publication">
                        Publication: <a :href="alumnus.publication.link">{{ alumnus.publication.title }}</a><br />
                    </template>
                    <template v-if="alumnus.thesis">
                        Thesis:
                        <span v-if="alumnus.thesisLink">
                            <a :href="alumnus.thesisLink">{{ alumnus.thesis }}</a>
                        </span>
